import { useState, useEffect, useRef } from "react"; 
import { Container, Row, Col, Tab } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import compilateur from "../assets/img/compilateur.jpg";
import machine from "../assets/img/machine-learning.jpg";
import son from "../assets/img/son.jpg";
import boat from "../assets/img/boat.jpg";
import portfolio from "../assets/img/portfolio.jpg";
import microprocesseur from "../assets/img/microprocesseur.jpg";

export const Projects = () => {
  const [columnCount, setColumnCount] = useState(3); // Par dfaut, 3 colonnes
  const sectionRef = useRef(null);

  const projects = [
    { title: "Compilateur", description: "Cration d'un language permettant la ralisation de plan de maison  partir d'un texte ou d'un mode interactif.", imgUrl: compilateur, url: "https://github.com/MelDubi/depot-ensta-compilateur" },
    { title: "Machine learning", description: "Travaux effectus  l'Ensta Bretagne en Machine Learning", imgUrl: machine, url: "https://github.com/MelDubi/depot-ensta-python" },
    { title: "Traitement du son", description: "Enregistrement d'un son de quelques secondes sur une carte STM32 fourni via un micro MEMS et rejou sur des hauts-parleurs via un DAC.", imgUrl: son, url: "https://github.com/MelDubi/depot-ensta-STM32-Traitement-Son" },
    { title: "Smart boat", description: "Ce projet met en uvre un systme intelligent intgrant une interface graphique bas sur egui et une commande vocale pour le contrle d'une maquette de bateau. Diffrents capteurs de navigation et des moteurs permettent de simuler une navigation en scurit et assist.", imgUrl: boat, url: "https://github.com/MelDubi/depot-smart-boat" },
    { title: "Portfolio", description: "Projet pour designer et dployer ce Portfolio", imgUrl: portfolio, url: "https://github.com/MelDubi/MD-Portfolio-Web" },
    { title: "Coming soon..", description: "Microprocesseur", imgUrl: microprocesseur, url: "https://github.com/MelDubi/depot-ensta-microprocesseur" },
  ];

  useEffect(() => {
    const handleResize = () => {
      // On ajuste la colonne en fonction de la largeur de l'cran
      if (window.innerWidth >= 1800) {
        setColumnCount(3); // 3 colonnes pour les grands crans
      } else if (window.innerWidth >= 1080) {
        setColumnCount(2); // 2 colonnes pour les crans moyens
      } else {
        setColumnCount(1); // 1 colonne pour les petits crans
      }
    };

    handleResize(); // On appelle une premire fois pour dfinir la valeur de `columnCount`

    // Ajout d'un couteur pour redimensionner
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize); // Nettoyage
  }, []);

  return (
    <section className="project" id="projects" ref={sectionRef}>
      <Container>
        <Row className="justify-content-center">
          <Col size={12}>
            <p style={{ fontSize: '20px', fontStyle: 'italic', textAlign: 'center', color: '#666' }}>
              Ce que j'ai fait..
            </p>
            <h2>Projets</h2>
            <p>Voici quelques-uns de mes projets raliss durant mon cursus scolaire ainsi que sur mon temps libre.</p>
            <Tab.Container id="projects-tabs" defaultActiveKey="first">
              <Tab.Content id="slideInUp">
                <Tab.Pane eventKey="first">
                  <Row style={{ display: 'flex', justifyContent: 'center', gap: '20px' }}>
                    {projects.map((project, index) => (
                      <Col
                        key={index}
                        className="project-col"
                        style={{
                          display: 'flex', 
                          justifyContent: 'center', // Centrer les cartes dans chaque colonne
                          marginBottom: '20px',
                          padding: '0', // Pas de padding pour les colonnes
                        }}
                      >
                        <div style={{ width: '400px' }}> {/* Largeur fixe pour chaque carte */}
                          <ProjectCard {...project} />
                        </div>
                      </Col>
                    ))}
                  </Row>
                </Tab.Pane>
              </Tab.Content>
            </Tab.Container>
          </Col>
        </Row>
      </Container>
    </section>
  );
};